body {
  margin: 0;
  padding: 0;
  /* font-family: Arial, Helvetica, sans-serif; */
}

.App {
  text-align: center;
  font-size: medium;
}

/* .AlwaysShow{
  position: fixed;
  min-width:100%;
} */

.TableContainer {
  padding: 5px;
  font-size: small;
  flex-grow: 2;
}

.dealtable {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.brandItemFilter {
  left: "10px";
  padding: "2px 20px";
  font-size: "small";
}

.branddealtable {
  display: flex;
}

.AllBrandsTableContainer {
  min-height: 80vh;
  padding: 10px;
}

.WrapperContainer {
  padding: 10px;
  min-height: 82vh;
  display: flex;
  flex-direction: column;
}

.Footer {
  padding: 10px;
  background-color: black;
  color: white;
  text-align: right;
  bottom: 0px;
  position: inherit;
  height: 7.5vh;
  width: 100%;
}

.App-header {
  background-color: #282c34;
  min-height: 5vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  font-size: x-large;
  color: white;
}

.App-link {
  color: #61dafb;
}

.itemimg {
  width: 300px;
  height: 300px;
  -webkit-animation-name: zoomin-d;
          animation-name: zoomin-d;
  -webkit-animation-duration: 0.4s;
          animation-duration: 0.4s;
}

@-webkit-keyframes zoomin-d {
  from {
    height: 200px;
    width: 200px;
  }

  to {
    height: 300px;
    width: 300px;
  }
}

@keyframes zoomin-d {
  from {
    height: 200px;
    width: 200px;
  }

  to {
    height: 300px;
    width: 300px;
  }
}

.chuppi {
  display: flex;
  flex-direction: column;
  width: 100%;
  
}

.jo {
  color: black;
}

.joe
{
