}
.row1 {
  display: flex;
  flex-direction: row;
  min-width: 80%;
}

.col-1 {
  display: flex;
  flex-direction: column;
  min-width: 180px;
}

.available-brands
{
  /* border-top: 1px solid gray; */
  margin-top: 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* flex: 1 0 auto; */
  min-width:inherit;
  word-wrap: break-word;
  height:40vh;
  overflow-y: scroll;
  overflow-x: hidden;
  text-align:left;
}

.brand-label{
  margin-bottom:3px;
  width:-webkit-fill-available;
  width:-moz-available;
  width:stretch;
