  /* display: block */
}

.chuppiInfo {
  padding: 5px;
  flex-grow: 1;
  display: flex;
  
}

.chuppiInfoChild{
  flex: 1 1;
  align-self: center
}

.searchBar {
  display: flex;
  align-items: stretch;
  order: 1;
  flex: 1 0 10%;
}
.pagination {
  display: flex;
  align-items: center;
  flex-direction: row;
}

.chuppiGrid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  padding: 5px;
  order: 2;
  min-width: 80%;
  /* min-height: 87vh */
}

.chuppiGridLabel{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  padding: 5px;
  order: 2;
  min-width: 80%;
  
}


.chuppiGridImage {
